@import url('https://fonts.googleapis.com/css?family=Montserrat:300,400');

body {
  font-family: 'Montserrat', sans-serif;
}

.App-logo {
  height: 40px;
}

.App-header {
  text-align: center;
  background-color: #222;
  padding: 10px 0;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

#google-map {
  position: relative;
}

.loading-map {
  height: calc(100vh - 64px);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.loading-map h1 {
  font-size: 24px;
  font-weight: 300;
}


